import { Component, OnInit } from '@angular/core';

declare var $: any;
@Component({
    selector: 'pub-banner',
    templateUrl: 'pub.component.html'
})

export class PubComponent implements OnInit {
    visibility: string;
    constructor() { }

    ngOnInit() {
        this.visibility = 'visible';
        $('.slick-container').slick({
            dots: true,
            infinite: true,
            arrows: false,
            speed: 500,
            fade: true,
            cssEase: 'linear',
             autoplay: true,
            autoplaySpeed: 3000,
            slidesToShow: 1,
            adaptiveHeight: true
        });
    }
}


