import { NgModule } from '@angular/core';
import { TranslateModule, TranslateLoader, LangChangeEvent, TranslateService } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { HttpModule, Http } from '@angular/http';

// AoT requires an exported function for factories
export function HttpLoaderFactory(http: Http) {
    return new TranslateHttpLoader(http, 'assets/i18n/', '.json');
}

@NgModule({
    imports: [
        TranslateModule.forRoot({
            loader: {
                provide: TranslateLoader,
                useFactory: HttpLoaderFactory,
                deps: [Http],
            },
            isolate: false
        })
    ],
    declarations: [],
    providers: [],
    exports: [
        TranslateModule,
    ]
})
export class SharedModule {
    constructor(public translate: TranslateService) {
        translate.addLangs(["fr", "be", "de", "es", "nl", "it"]);
        translate.setDefaultLang('fr');
        translate.setDefaultLang('be');
        translate.setDefaultLang('de');
        translate.setDefaultLang('es');
        translate.setDefaultLang('nl');
        translate.setDefaultLang('it');
        let browserLang = translate.getBrowserLang();
        var user = JSON.parse(localStorage.getItem('currentUser'));
        let country = user != null ? user.country : null;
        let lang = country != null ? country.key : null;
        browserLang = browserLang.match(/en|es|it|nl|de|be|fr/) ? browserLang : 'fr';
        translate.use(lang ? lang : browserLang);
    }
}


