import { Component, OnInit } from '@angular/core';


@Component({
  selector: 'app-dashboard',
  templateUrl: './full-layout.component.html'
})
export class FullLayoutComponent implements OnInit {
  public firstname: string = "";
  public lastname: string = "";
  public civility: string = "";
  constructor() {
    var currentUser: any = JSON.parse(localStorage.getItem('currentUser'));
    var customer = currentUser != null ? (currentUser.hasOwnProperty('customer') ? currentUser.customer : null) : null;
    console.log(customer);
    if (customer !== null) {
      this.firstname = customer.firstName;
      this.lastname = customer.lastName;
      this.civility = customer.civility;
    }
  }
  public hidDiv: boolean = false;
  public disabled: boolean = false;
  public status: { isopen: boolean } = { isopen: false };
  public date = new Date();

  public toggled(open: boolean): void {

  }
  showSettings(): void {
    this.hidDiv = !this.hidDiv;
  }
  public toggleDropdown($event: MouseEvent): void {
    $event.preventDefault();
    $event.stopPropagation();
    this.status.isopen = !this.status.isopen;
  }
  logout() {
    localStorage.removeItem('currentUser');
  }

  ngOnInit(): void { }

}



